import React from 'react'
import './loader.css'
import styled from 'styled-components';

export const LoaderContainer = styled.section`
  width: ${({width})=>(width ? width + 'rem':'95%')}
`;


export const Loader = ({width}) => {
 return(
  <LoaderContainer className='loader' width={width}>
  <div></div>
  <div></div>
  <div></div>
  <div></div>
  <div></div>
 </LoaderContainer>
 )
}

export default Loader